				<!-- <uni-icons type="arrow-right" color = "#FFC300" ></uni-icons> -->
	        </view>
	        <view class="card-1-3">
				<progress :percent="customer.points"  backgroundColor="#EBEBEB" activeColor="#FFC300" show-info stroke-width="6" />
	        </view>
			<view  class="card-1-5">
			  <view  @click="bindtap"data-type="shop">
			   	<uni-icons type="shop"  size="60"></uni-icons>
			    <!-- <view>宠物商城</view> -->
			  </view>
