			<uni-icons type="star" ></uni-icons>
	        <text class="card-title-text">优惠券</text>
	      </view>
	      <view class="flex-end card-2-dec " data-type="coupon"  @click="bindtap">
			  <view class="icon">
			    <text>领取优惠券</text>
			    <uni-icons type="arrow-right"></uni-icons>
			  </view>
		  </view>
	      <view class="card-2-dec card-2-con" v-if="coupon != null">
			  <scroll-view :scroll-x="true">
				  <view class="coupon coupon-wave-left coupon-wave-right coupon-yellow-gradient">
					  <view class="coupon-info coupon-hole coupon-info-right-dashed">
							 <!-- 使用范围，0—全场，1—商品
							 优惠卷类型, 0-满减, 1-折扣 2—直减-->
							 <block v-if="coupon.rule.type === 0">
								<block v-if="coupon.rule.use_range === 0">
									<view class="coupon-price">¥{{coupon.rule.amount}}</view>
									<view class="coupon-desc">【全场 满减】{{coupon.coupon_batch.coupon_name}}</view>
									<view class="coupon-expiry-date">有效期：{{coupon.rule.use_started_at}}至{{coupon.rule.use_ended_at}}</view>
								</block>
								<block v-if="coupon.rule.use_range === 1">
									<view class="coupon-price">¥{{coupon.rule.amount}}</view>
									<view class="coupon-desc">【商品 满减】{{coupon.coupon_batch.coupon_name}}</view>
									<view class="coupon-expiry-date">有效期：{{coupon.rule.use_started_at}}至{{coupon.rule.use_ended_at}}</view>
								</block>
							 </block>
							 <block v-if="coupon.rule.type === 1">
								<block v-if="coupon.rule.use_range === 0">
									<view class="coupon-price">¥{{coupon.rule.amount}}</view>
									<view class="coupon-desc">【全场 折扣】{{coupon.coupon_batch.coupon_name}}</view>
									<view class="coupon-expiry-date">有效期：{{coupon.rule.use_started_at}}至{{coupon.rule.use_ended_at}}</view>
								</block>
								<block v-if="coupon.rule.use_range === 1">
									<view class="coupon-price">¥{{coupon.rule.amount}}</view>
									<view class="coupon-desc">【商品 折扣】{{coupon.coupon_batch.coupon_name}}</view>
									<view class="coupon-expiry-date">有效期：{{coupon.rule.use_started_at}}至{{coupon.rule.use_ended_at}}</view>
								</block>
							 </block>
							 <block v-if="coupon.rule.type === 2">
								<block v-if="coupon.rule.use_range === 0">
									<view class="coupon-price">¥{{coupon.rule.amount}}</view>
									<view class="coupon-desc">【全场 直减 无规则】{{coupon.coupon_batch.coupon_name}}</view>
									<view class="coupon-expiry-date">有效期：{{coupon.rule.use_started_at}}至{{coupon.rule.use_ended_at}}</view>
											
								</block>
								<block v-if="coupon.rule.use_range === 1">
									<view class="coupon-price">¥{{coupon.rule.amount}}</view>
									<view class="coupon-desc">【商品 直减 无规则】{{coupon.coupon_batch.coupon_name}}</view>
									<view class="coupon-expiry-date">有效期：{{coupon.rule.use_started_at}}至{{coupon.rule.use_ended_at}}</view>
								</block>
							 </block>
						</view>
						<view class="coupon-get coupon-get-already">已领取</view>
				  </view>
			  </scroll-view>
	      </view>
	    </view>

	    <view class="card card-3">
	      <view class="card-title" data-type="userOrder" @click="bindtap">
	        <uni-icons type="list" ></uni-icons>
	        <text class="card-title-text">我的订单</text>
	        <view class="icon">
	          <text>查看所有订单</text>
	          <uni-icons type="arrow-right"></uni-icons>
	        </view>
	      </view>
	    </view>
	    <view class="card card-3">
	      <view class="card-title" data-type="comment" @click="bindtap">
	        <uni-icons type="chatboxes" ></uni-icons>
	        <text class="card-title-text">我的评论</text>
	        <view class="icon">
	          <uni-icons type="arrow-right"></uni-icons>
	        </view>
	      </view>
	    </view>
		
	
	    <view class="card card-3">
	      <view class="card-title" data-type="customer" @click="bindtap">
	        	<uni-icons type="person" ></uni-icons>
	        <text class="card-title-text">基本信息</text>
	      </view>
	    </view>
	
	    <view class="card card-3">
	      <view class="card-title" data-type="userAddress" @click="bindtap">
	      	<uni-icons type="compose" ></uni-icons>
	        <text class="card-title-text">地址管理</text>
	        <view class="icon">
	              <uni-icons type="arrow-right" ></uni-icons>
	        </view>
	      </view>
	    </view>
	    <view class="card card-3">
	      <view class="card-title" data-type="aboutUs" @click="bindtap">
	        	<uni-icons type="info" ></uni-icons>
	        <text class="card-title-text">关于我们</text>
	      </view>
	    </view>
	    <view class="card card-3">
	      <view class="card-title" data-type="onlineChat" @click="bindtap">
	        <uni-icons type="staff" ></uni-icons>
	        <text class="card-title-text">在线客服</text>
	      </view>
	    </view>
	    <view class="card card-3">
	      <view class="card-title" data-type="clearCache" @click="bindtap">
			<uni-icons type="trash" ></uni-icons>
	        <text class="card-title-text">清除缓存</text>
	      </view>
	    </view>
	  </view>
	  <view class="footer">2022  yejiaYang</view>
	</view>
	
</template>

<script>
	import http from '@/utils/request.js'
	// import showToast from '@/utils/request.js'
	import { pathToBase64, base64ToPath } from 'image-tools'
	const unVerfiy = ["aboutUs","clearCache"];
	export default {
		data() {
			return {
				customer: getApp().globalData.customer,
				Token:"",
				hasLogin:  getApp().globalData.hasLogin,
				title: '橘作坊',
				Token:'',
				coupon:null
			}
		},
		onLoad() {
			let myApp = this;
		},
		onShow() {
			var myApp = this;
			
			myApp.hasLogin = getApp().globalData.hasLogin
			// myApp.hasLogin = true
			myApp.customer = getApp().globalData.customer
			if(typeof myApp.customer.id === "undefined" ){
				return
			}
			// myApp.loadCoupon()
		},
		methods: {
			loadCoupon(){
				var myApp = this
				http("coupon/getOne", {
					'customer_id': myApp.customer.id,
				}, {
					hideLoading: false, // 默认 false
					hideMsg: true, // 默认 false
				}).then(res => {
					
					myApp.coupon = res.data
				}).catch(err => {
					console.error(err)
				})
			},
			showToast(title,icon){
				if(typeof icon === "undefined" ){
					icon = 'none';
				}
				uni.showToast({
					title: title,
					icon: icon
				})	
			},
			
			pathToBase64Tool(path){
				var myApp = this
				console.info(path)
				pathToBase64(path)
				  .then(base64 => {
					myApp.customer.avatarUrl  = base64
					uni.setStorageSync('customer',myApp.customer);
					let url = '/pages/auth/login/login'
					myApp.jumpPage(url);
				  })
				  .catch(error => {
					console.error(error)
				  })
			},
			OnChooseAvatar(e){
				var myApp = this
				myApp.pathToBase64Tool(e.detail.avatarUrl);
				
			},
			
			
			 // 获取手机号 非企业号不可用
			 /**
			getPhoneNumber(e) {
				
				 this.getCode();
				 if(!this.decodePhoneParams.code || !e.detail.encryptedData){
						 return false;
				 }
				 this.decodePhoneParams.encryptedData = e.detail.encryptedData;
				 this.decodePhoneParams.iv = e.detail.iv;
				 this.$u.api
						 .postDecodeUserInfo(this.decodePhoneParams)
						 .then(res => {
								 console.log(res, '[地址管理-获取用户手机号]');
								 this.params.mobile = res.data.phoneNumber
						 })
						 .catch(err => {
								 console.log(err);
						 });
			 },
			 * **/
			

			bindtap(e){
				var myApp = this;
				var type = e.currentTarget.dataset.type;
				let url = "";
				// console.info(getApp().globalData.hasLogin);
				if(!getApp().globalData.hasLogin && !unVerfiy.includes(type)) {
					myApp.showToast("您还未登陆，无法查看此功能")
					return;
				}
				if(type=="userOrder"){
				  url = "/pages/order/order/order";
				}else if(type=="coupon"){
				  url = "/pages/coupon/coupon/coupon";
				}else if(type=="customer"){
				  url = "/pages/user/mine/mine/mine";
				}else if(type=="userAddress"){
				  url = "/pages/address/address/address";
				}else if(type=="shop"){
				  url = "/pages/shop/shop/shop";
				}else if(type=="comment"){
				  url = "/pages/comment/comment/comment";
				}else if(type=="clearCache"){
				  uni.showModal({
				  	title: '清除缓存',
				  	content: '是否确定清除所有缓存？',
				  	success: function (res) {
				  		if (res.confirm) {
				  			uni.clearStorage();
							myApp.showToast("清除成功","success")
							getApp().globalData.hasLogin = false
							myApp.hasLogin = false
				  		} else if (res.cancel) {
							myApp.showToast("已取消")
				  		}
				  	}
				  });
				}else if(type=="aboutUs"){
					uni.showModal({
						title: '小程序信息',
						content: '个人开发者wx Fiveisme001',
						showCancel: false,
						success: function (res) {
							if (res.confirm) {
								uni.showToast({
									title:"欢迎再来～",
									icon:'none',
									duration:500
								})
							} 
						}
					});
				}
				
				myApp.jumpPage(url);
				
			},
			jumpPage(url){
				uni.navigateTo({
					url: url,
					events: {
					    acceptDataFromOpenedPage: function(data) {
					      console.log(data)
					    },
					    someEvent: function(data) {
					      console.log(data)
					    }
					  },
					  success: function(res) {
					    // 通过eventChannel向被打开页面传送数据
					    res.eventChannel.emit('acceptDataFromOpenerPage', { data: 'data from starter page' })
					  }
				});
			},
		}
	}
</script>

